.card {
  background: #fff;
  border-radius: 2px;
  height: 250px;
  margin: 1rem;
  position: relative;
  width: 250px;
  box-shadow: 0 3px 6px #333, 0 3px 6px #333;
  text-align: left;
  border-radius: 100%;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  border-radius: 100%;
}
.card > .img-container > img {
  width: 100%;
  border-radius: 100%;
  border: 1px solid #fff;
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

.wrapper {
  padding-top: 50px;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.header{
  width: 100%;
  text-align: center;
  font-size: 3rem;
  color: #fff;
  margin-bottom: 50px;
  line-height: 0;
}

h1 {
  padding-bottom: 1rem;
}

.scores {
  margin: 0 auto;
  width: 40%;
}

p {
  font-size: 1.5rem;
  width: 50%;
  display: inline-block;
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
  background-image: url('https://github.com/zcdev/clicky-owls/blob/master/public/images/forest.jpg?raw=true');
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
}

